.swiper-slide {

margin-right: 10px;
}

.swiper-wrapper{

position: relative;
}
 
.slider-navigation {
display: flex;

align-items: center;
position: relative;
top: -34px;
left: -125px;
gap: 40px;
}

.slider-next-button {
width: 30px; 
height: 30px; 
cursor: pointer;
width: 15px;
height: 15px;
border: 2px solid #333;
border-left: 0;
border-top: 0;
transform: rotate(315deg);
opacity: 30%;
transition: all .3s;

}

.slider-prev-button {
cursor: pointer;
width: 15px;
height: 15px;
border: 2px solid #333;
border-left: 0;
border-top: 0;
transform: rotate(135deg);
opacity: 30%;
transition: all .3s;
}
.slider-prev-button:hover , .slider-next-button:hover {
opacity: 100%;
}

.slider-prev-button::before,
.slider-next-button::before {
content: '';
position: absolute;
width: 250%;
height: 250%;
border-radius: 50%;
border: 2px solid rgb(0, 0, 0);
box-sizing: border-box;
top: 0;
left: 0;
opacity: 30%;
}

.slider-prev-button::before {
top: 50%;
left: 50%;
transform: translate(-40%, -40%);
}

.slider-next-button::before {
top: 50%;
left: 50%;
transform: translate(-40%, -40%);
}
.business__dashedCircle {
position: absolute;
width: 604px;
height: 598px;
border: 2px dashed black;
border-radius: 50%;
top: 45px;
right: -370px;
z-index: -1;
}

.swiper-slide {
	flex-shrink: 1;
}