@import url('https://fonts.googleapis.com/css2?family=Hind+Vadodara:wght@300;400;600;700&family=Oswald:wght@300;400&display=swap');

body {
font-family: 'Hind Vadodara', sans-serif;
box-sizing: border-box;
overflow-x: hidden;
}

body::after {
content: "";
background-image: url(data:image/png;base64,);
width: 555.538px;
height: 819.823px;
position: absolute;
top: 0;
right: 0;
background-repeat: no-repeat;
z-index: -1;
}

[class$="__container"] {
max-width: 1330px;
margin: 0 auto;
padding: 0 15px;
}

.header__container .header__content {
display: flex;
justify-content: space-between;
align-items: center;
margin-top: 43px;
margin-bottom: 162px;
}

.header__list {
display: flex;
gap: 20px;
width: 352px;
}

.header__item {
position: relative;
}

.header__item-text {
color: #331B3B;
font-size: 16px;
font-weight: 400;
letter-spacing: 0.16px;
transition: text-shadow 0.3s ease;
position: relative;
}

.header__item-text::before {
content: '';
position: absolute;
left: 0;
bottom: -3px;
width: 0;
height: 1.5px;
background-color: rgb(255, 251, 0);
transition: width 0.35s;
}

.header__item-text:hover::before {
width: 100%;
}

.header__item:hover .header__item-text {
text-shadow: 0 0 .45px #292727, 0 0 .45px #2b2929;
}

.header__social {
display: flex;
justify-content: center;
align-items: center;
gap: 40px;
cursor: pointer;
}

.header__social-link::before {
content: "";
position: absolute;
top: -12px;
left: -9px;
width: 30px;
height: 100%;
background-image: url("data:image/svg+xml;charset=utf-8,<svg xmlns='http://www.w3.org/2000/svg' width='46' height='49' viewBox='0 0 46 49' fill='none'><path d='M40.7347 35.3674C37.8086 40.3349 32.7551 44.2964 27.1723 46.2689C21.5909 48.2409 15.5957 48.1894 10.7238 45.3196C5.8519 42.4498 2.90043 37.2311 1.91937 31.3935C0.938064 25.5544 1.95301 19.2139 4.87912 14.2465C7.80523 9.27901 12.8587 5.31748 18.4415 3.34498C24.0229 1.37298 30.0181 1.42444 34.89 4.29427C39.7619 7.16409 42.7134 12.3827 43.6945 18.2204C44.6758 24.0595 43.6608 30.4 40.7347 35.3674Z' stroke='%23331B3B' stroke-opacity='0.64' stroke-width='2'/></svg>");
background-repeat: no-repeat;
z-index: -1;
width: 46px;
height: 49.614px;
}

.header__social-link {
display: inline-block;
cursor: pointer;
position: relative;
top: 1px;
left: 3px;
width: 27px;
text-align: center;
}

